
"use client";

import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Upload, Briefcase, Users, FileText, TrendingUp, Calendar, Bell, ChevronRight } from "lucide-react";
import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';

export const FacultyDashboard = () => {
    const { user } = useAuth();
    const [stats, setStats] = useState({
        totalResources: 0,
        totalDownloads: 0,
        activeOpportunities: 0,
        totalApplicants: 0
    });
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchStats = async () => {
            if (!user) return;
            try {
                const token = await user.getIdToken();
                const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

                // Fetch opportunities
                const oppRes = await fetch(`${baseUrl}/opportunities/list?facultyId=${user.uid}`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });
                const opportunities = await oppRes.json();

                // Fetch applications
                const appRes = await fetch(`${baseUrl}/opportunities/applications`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });
                const applications = await appRes.json();

                // Fetch resources
                const resRes = await fetch(`${baseUrl}/academic/list`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });
                const resources = await resRes.json();
                const myResources = resources.filter((r: any) => r.uploadedBy === user.uid);

                setStats({
                    totalResources: myResources.length,
                    totalDownloads: myResources.reduce((sum: number, r: any) => sum + (r.downloadCount || 0), 0),
                    activeOpportunities: opportunities.filter((o: any) => o.status === 'Open').length,
                    totalApplicants: applications.length
                });
            } catch (error) {
                console.error('Error fetching stats:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchStats();
    }, [user]);

    return (
        <div className="space-y-10 animate-in fade-in-50 duration-500 pb-10">
            <header>
                <h2 className="text-3xl font-bold tracking-tight mb-2 text-slate-900 dark:text-slate-50">Faculty Dashboard</h2>
                <p className="text-muted-foreground">Manage academic resources, research opportunities, and student engagement.</p>
            </header>

            {/* Quick Stats */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <StatCard
                    title="Resources Shared"
                    value={loading ? "..." : stats.totalResources}
                    icon={FileText}
                    color="text-blue-600"
                    bgColor="bg-blue-50 dark:bg-blue-900/20"
                />
                <StatCard
                    title="Engagement"
                    value={loading ? "..." : stats.totalDownloads}
                    icon={TrendingUp}
                    color="text-emerald-600"
                    bgColor="bg-emerald-50 dark:bg-emerald-900/20"
                />
                <StatCard
                    title="Open Roles"
                    value={loading ? "..." : stats.activeOpportunities}
                    icon={Briefcase}
                    color="text-indigo-600"
                    bgColor="bg-indigo-50 dark:bg-indigo-900/20"
                />
                <StatCard
                    title="Total Applicants"
                    value={loading ? "..." : stats.totalApplicants}
                    icon={Users}
                    color="text-amber-600"
                    bgColor="bg-amber-50 dark:bg-amber-900/20"
                />
            </div>

            {/* Quick Actions */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <Card className="bg-gradient-to-br from-primary/[0.03] to-transparent border-primary/10 shadow-sm">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2 text-lg font-bold">
                            <Upload className="w-5 h-5 text-primary" />
                            Academic Operations
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <Link href="/faculty/upload" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <Upload className="w-4 h-4 mr-3 text-primary" />
                                    Upload Academic Resource
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                        <Link href="/faculty/opportunities" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <Briefcase className="w-4 h-4 mr-3 text-primary" />
                                    Post Research Opportunity
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                        <Link href="/faculty/applications" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <Users className="w-4 h-4 mr-3 text-primary" />
                                    Review Pending Applications
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                    </CardContent>
                </Card>

                {/* Recent Activity */}
                <Card className="shadow-sm border-border/50">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2 text-lg font-bold">
                            <Bell className="w-5 h-5 text-primary" />
                            Real-time Activity
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-2">
                            <ActivityItem
                                title="New Application Received"
                                subtitle="Research Assistant position"
                                time="2h ago"
                                icon={Users}
                                iconColor="text-blue-600"
                                iconBg="bg-blue-50 dark:bg-blue-900/20"
                            />
                            <ActivityItem
                                title="Resource Downloaded"
                                subtitle="Week 5 Lecture Notes"
                                time="5h ago"
                                icon={FileText}
                                iconColor="text-emerald-600"
                                iconBg="bg-emerald-50 dark:bg-emerald-900/20"
                            />
                            <ActivityItem
                                title="Deadline Reminder"
                                subtitle="Internship applications close in 3 days"
                                time="1d ago"
                                icon={Calendar}
                                iconColor="text-indigo-600"
                                iconBg="bg-indigo-50 dark:bg-indigo-900/20"
                            />
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
};

const ActivityItem = ({ title, subtitle, time, icon: Icon, iconColor, iconBg }: any) => (
    <div className="flex items-center gap-4 p-3 rounded-xl hover:bg-muted/50 transition-all cursor-pointer group border border-transparent hover:border-border/50">
        <div className={`w-9 h-9 rounded-xl ${iconBg} ${iconColor} flex items-center justify-center shrink-0 group-hover:scale-105 transition-transform`}>
            <Icon className="w-4 h-4" />
        </div>
        <div className="flex-1 min-w-0">
            <p className="text-sm font-semibold truncate">{title}</p>
            <p className="text-xs text-muted-foreground truncate">{subtitle}</p>
        </div>
        <span className="text-xs text-muted-foreground font-medium shrink-0">{time}</span>
    </div>
);

const StatCard = ({ title, value, icon: Icon, color, bgColor }: any) => (
    <Card className="border-border/50 shadow-sm hover:shadow-md transition-all duration-200">
        <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
                <div className={`p-2.5 rounded-xl ${bgColor} ${color}`}>
                    <Icon className="w-5 h-5" />
                </div>
            </div>
            <div>
                <p className="text-sm font-medium text-muted-foreground mb-1">{title}</p>
                <div className="text-3xl font-bold tracking-tight">{value}</div>
            </div>
        </CardContent>
    </Card>
);
