
"use client";

import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Users, Shield, Briefcase, FileText, TrendingUp, Activity, AlertCircle, Download } from "lucide-react";
import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';

export const AdminDashboard = () => {
    const { user } = useAuth();
    const [stats, setStats] = useState({
        users: {
            total: 0,
            students: 0,
            faculty: 0,
            authority: 0,
            admin: 0
        },
        grievances: {
            total: 0,
            pending: 0,
            resolved: 0
        },
        opportunities: {
            total: 0,
            applications: 0
        },
        resources: {
            total: 0,
            downloads: 0
        },
        dailyActiveUsers: 0
    });
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchStats = async () => {
            if (!user) return;
            try {
                const token = await user.getIdToken();
                const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

                const response = await fetch(`${baseUrl}/admin/stats`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });

                if (!response.ok) throw new Error('Failed to fetch stats');
                const data = await response.json();
                setStats(data);
            } catch (error) {
                console.error('Error fetching stats:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchStats();
    }, [user]);

    return (
        <div className="space-y-10 animate-in fade-in-50 duration-500 pb-10">
            <header>
                <h2 className="text-3xl font-bold tracking-tight mb-2 text-slate-900 dark:text-slate-50">System Administration</h2>
                <p className="text-muted-foreground">Monitor platform health, manage users, and review system-wide analytics.</p>
            </header>

            {/* System Overview */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <StatCard
                    title="Total Users"
                    value={loading ? "..." : stats.users.total}
                    icon={Users}
                    color="text-blue-600"
                    bgColor="bg-blue-100 dark:bg-blue-900/20"
                    subtitle={`${stats.users.students} students`}
                />
                <StatCard
                    title="Total Grievances"
                    value={loading ? "..." : stats.grievances.total}
                    icon={Shield}
                    color="text-orange-600"
                    bgColor="bg-orange-100 dark:bg-orange-900/20"
                    subtitle={`${stats.grievances.pending} pending`}
                />
                <StatCard
                    title="Opportunities"
                    value={loading ? "..." : stats.opportunities.total}
                    icon={Briefcase}
                    color="text-purple-600"
                    bgColor="bg-purple-100 dark:bg-purple-900/20"
                    subtitle={`${stats.opportunities.applications} applications`}
                />
                <StatCard
                    title="Resources"
                    value={loading ? "..." : stats.resources.total}
                    icon={FileText}
                    color="text-green-600"
                    bgColor="bg-green-100 dark:bg-green-900/20"
                    subtitle={`${stats.resources.downloads} downloads`}
                />
            </div>

            {/* Quick Actions */}
            <Card className="bg-gradient-to-br from-primary/5 to-transparent border-primary/10 shadow-sm">
                <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                        <Activity className="w-5 h-5" />
                        Quick Actions
                    </CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                        <Link href="/admin/users" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <Users className="w-4 h-4 mr-2" />
                                Manage Users
                            </Button>
                        </Link>
                        <Link href="/admin/roles" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <Shield className="w-4 h-4 mr-2" />
                                Assign Roles
                            </Button>
                        </Link>
                        <Link href="/admin/analytics" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <TrendingUp className="w-4 h-4 mr-2" />
                                View Analytics
                            </Button>
                        </Link>
                        <Link href="/admin/audit-logs" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <FileText className="w-4 h-4 mr-2" />
                                Audit Logs
                            </Button>
                        </Link>
                        <Link href="/admin/reports" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <Download className="w-4 h-4 mr-2" />
                                Export Reports
                            </Button>
                        </Link>
                        <Link href="/admin/system-health" className="block">
                            <Button className="w-full justify-start" variant="outline">
                                <Activity className="w-4 h-4 mr-2" />
                                System Health
                            </Button>
                        </Link>
                    </div>
                </CardContent>
            </Card>

            {/* User Distribution */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <Card className="shadow-sm">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <Users className="w-5 h-5" />
                            User Distribution by Role
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-4">
                            <RoleDistributionBar
                                role="Students"
                                count={stats.users.students}
                                total={stats.users.total}
                                color="bg-blue-500"
                            />
                            <RoleDistributionBar
                                role="Faculty"
                                count={stats.users.faculty}
                                total={stats.users.total}
                                color="bg-purple-500"
                            />
                            <RoleDistributionBar
                                role="Authority"
                                count={stats.users.authority}
                                total={stats.users.total}
                                color="bg-orange-500"
                            />
                            <RoleDistributionBar
                                role="Admin"
                                count={stats.users.admin}
                                total={stats.users.total}
                                color="bg-green-500"
                            />
                        </div>
                    </CardContent>
                </Card>

                {/* System Status */}
                <Card className="shadow-sm">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <Activity className="w-5 h-5" />
                            System Status
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-4">
                            <StatusItem
                                label="API Status"
                                status="Operational"
                                color="text-green-600"
                            />
                            <StatusItem
                                label="Database"
                                status="Healthy"
                                color="text-green-600"
                            />
                            <StatusItem
                                label="Storage"
                                status="Normal"
                                color="text-green-600"
                            />
                            <StatusItem
                                label="Daily Active Users"
                                status={`${stats.dailyActiveUsers} users`}
                                color="text-blue-600"
                            />
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Recent Activity Summary */}
            <Card className="shadow-sm">
                <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                        <AlertCircle className="w-5 h-5" />
                        Platform Overview
                    </CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        <div className="space-y-2">
                            <h4 className="font-semibold text-sm text-muted-foreground">Grievances</h4>
                            <div className="space-y-1">
                                <div className="flex justify-between text-sm">
                                    <span>Total</span>
                                    <Badge variant="secondary">{stats.grievances.total}</Badge>
                                </div>
                                <div className="flex justify-between text-sm">
                                    <span>Pending</span>
                                    <Badge className="bg-orange-100 text-orange-800">{stats.grievances.pending}</Badge>
                                </div>
                                <div className="flex justify-between text-sm">
                                    <span>Resolved</span>
                                    <Badge className="bg-green-100 text-green-800">{stats.grievances.resolved}</Badge>
                                </div>
                            </div>
                        </div>
                        <div className="space-y-2">
                            <h4 className="font-semibold text-sm text-muted-foreground">Opportunities</h4>
                            <div className="space-y-1">
                                <div className="flex justify-between text-sm">
                                    <span>Active</span>
                                    <Badge variant="secondary">{stats.opportunities.total}</Badge>
                                </div>
                                <div className="flex justify-between text-sm">
                                    <span>Applications</span>
                                    <Badge className="bg-blue-100 text-blue-800">{stats.opportunities.applications}</Badge>
                                </div>
                            </div>
                        </div>
                        <div className="space-y-2">
                            <h4 className="font-semibold text-sm text-muted-foreground">Resources</h4>
                            <div className="space-y-1">
                                <div className="flex justify-between text-sm">
                                    <span>Uploaded</span>
                                    <Badge variant="secondary">{stats.resources.total}</Badge>
                                </div>
                                <div className="flex justify-between text-sm">
                                    <span>Downloads</span>
                                    <Badge className="bg-purple-100 text-purple-800">{stats.resources.downloads}</Badge>
                                </div>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
};

const StatCard = ({ title, value, icon: Icon, color, bgColor, subtitle }: any) => (
    <Card className="overflow-hidden border-border/50 shadow-sm hover:shadow-md transition-shadow duration-200">
        <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
                <div className={`p-3 rounded-xl ${bgColor} ${color}`}>
                    <Icon className="w-6 h-6" />
                </div>
            </div>
            <div>
                <p className="text-sm font-medium text-muted-foreground mb-1">{title}</p>
                <div className="text-3xl font-bold mb-1">{value}</div>
                {subtitle && <p className="text-xs text-muted-foreground">{subtitle}</p>}
            </div>
        </CardContent>
    </Card>
);

const RoleDistributionBar = ({ role, count, total, color }: any) => {
    const percentage = total > 0 ? Math.round((count / total) * 100) : 0;

    return (
        <div>
            <div className="flex items-center justify-between mb-2">
                <span className="text-sm font-medium">{role}</span>
                <span className="text-sm text-muted-foreground">{count} ({percentage}%)</span>
            </div>
            <div className="w-full bg-muted rounded-full h-2.5 overflow-hidden">
                <div
                    className={`h-full ${color} transition-all duration-500 rounded-full`}
                    style={{ width: `${percentage}%` }}
                />
            </div>
        </div>
    );
};

const StatusItem = ({ label, status, color }: any) => (
    <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50 hover:bg-muted/70 transition-colors">
        <span className="text-sm font-medium">{label}</span>
        <span className={`text-sm font-semibold ${color}`}>{status}</span>
    </div>
);
