
import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { FileText, BookOpen, Briefcase, ChevronRight, AlertCircle, Clock, CheckCircle, Shield } from "lucide-react";
import { Button } from "@/components/ui/button";

export const StudentDashboard = () => (
    <div className="space-y-10 animate-in fade-in-50 duration-500 pb-10">
        <header>
            <h2 className="text-3xl font-bold tracking-tight mb-2 text-slate-900 dark:text-slate-50">Student Command Center</h2>
            <p className="text-muted-foreground">Manage your campus life, academics, and career growth.</p>
        </header>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <DashboardCard
                title="Support & Feedback"
                link="/grievances"
                icon={FileText}
                color="text-rose-600"
                bgColor="bg-rose-50 dark:bg-rose-900/20"
                description="Track status & file new grievances"
            />
            <DashboardCard
                title="Learning Resources"
                link="/resources"
                icon={BookOpen}
                color="text-blue-600"
                bgColor="bg-blue-50 dark:bg-blue-900/20"
                description="Access study materials & papers"
            />
            <DashboardCard
                title="Career Portal"
                link="/opportunities"
                icon={Briefcase}
                color="text-emerald-600"
                bgColor="bg-emerald-50 dark:bg-emerald-900/20"
                description="Internships & research roles"
            />
            <DashboardCard
                title="Academic Manager"
                link="/student/courses"
                icon={BookOpen}
                color="text-indigo-600"
                bgColor="bg-indigo-50 dark:bg-indigo-900/20"
                description="Monitor attendance & course credits"
            />
            <DashboardCard
                title="My Applications"
                link="/student/applications"
                icon={Briefcase}
                color="text-amber-600"
                bgColor="bg-amber-50 dark:bg-amber-900/20"
                description="Review your active applications"
            />
            <DashboardCard
                title="Fiscal Insights"
                link="/student/ledger"
                icon={Clock}
                color="text-slate-600"
                bgColor="bg-slate-100 dark:bg-slate-800/50"
                description="Track transactions & deadlines"
            />
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            <Card className="shadow-sm border-border/50">
                <CardHeader className="flex flex-row items-center justify-between pb-4 space-y-0">
                    <CardTitle className="text-xl font-bold">Recent Updates</CardTitle>
                    <Button variant="ghost" size="sm" className="text-xs font-semibold hover:bg-muted/80">View Activity</Button>
                </CardHeader>
                <CardContent>
                    <div className="space-y-4">
                        <ActivityItem
                            title="Grievance #1234 Resolved"
                            description="Your request regarding WiFi has been addressed."
                            time="2h ago"
                            icon={CheckCircle}
                            iconColor="text-green-600"
                            iconBg="bg-green-50 dark:bg-green-900/20"
                        />
                        <ActivityItem
                            title="New Resource Added"
                            description="Prof. Smith uploaded 'Week 5 Slides'."
                            time="5h ago"
                            icon={BookOpen}
                            iconColor="text-blue-600"
                            iconBg="bg-blue-50 dark:bg-blue-900/20"
                        />
                    </div>
                </CardContent>
            </Card>

            <Card className="bg-gradient-to-br from-primary/[0.03] to-transparent border-primary/10 shadow-sm overflow-hidden relative">
                <div className="absolute top-0 right-0 p-8 opacity-5">
                    <Shield className="w-32 h-32" />
                </div>
                <CardHeader>
                    <CardTitle className="text-lg font-bold">Campus Support</CardTitle>
                </CardHeader>
                <CardContent className="space-y-6 relative z-10">
                    <p className="text-sm text-muted-foreground leading-relaxed">
                        Need assistance with your account or campus services? Our support team is available 24/7 to help you resolve any technical or administrative issues.
                    </p>
                    <div className="flex gap-3">
                        <Button className="flex-1 shadow-sm shadow-primary/20">
                            Get Help
                        </Button>
                        <Button variant="outline" className="flex-1">
                            Documentation
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </div>
    </div>
);

const ActivityItem = ({ title, description, time, icon: Icon, iconColor, iconBg }: any) => (
    <div className="flex items-center gap-4 p-4 rounded-xl hover:bg-muted/50 transition-all cursor-pointer border border-transparent hover:border-border/50 group">
        <div className={`w-10 h-10 rounded-xl ${iconBg} ${iconColor} flex items-center justify-center shrink-0 group-hover:scale-105 transition-transform`}>
            <Icon className="w-5 h-5" />
        </div>
        <div className="flex-1 min-w-0">
            <h4 className="text-sm font-semibold truncate">{title}</h4>
            <p className="text-xs text-muted-foreground truncate">{description}</p>
        </div>
        <span className="text-xs text-muted-foreground font-medium shrink-0">{time}</span>
    </div>
);

const DashboardCard = ({ title, link, icon: Icon, color, bgColor, description }: any) => {
    return (
        <Link href={link}>
            <Card className="group border-border/50 shadow-sm hover:shadow-md transition-all duration-300 hover:border-primary/20 h-full overflow-hidden">
                <CardContent className="p-6 flex flex-col h-full bg-gradient-to-br from-white to-slate-50/50 dark:from-slate-900 dark:to-slate-900/50">
                    <div className="flex items-center justify-between mb-4">
                        <div className={`p-2.5 rounded-xl ${bgColor} ${color} transition-transform group-hover:scale-110 duration-300`}>
                            <Icon className="w-5 h-5" />
                        </div>
                        <ChevronRight className="w-4 h-4 text-muted-foreground/30 group-hover:text-primary group-hover:translate-x-1 transition-all" />
                    </div>
                    <div>
                        <h3 className="text-lg font-bold tracking-tight mb-1 group-hover:text-primary transition-colors">{title}</h3>
                        <p className="text-xs text-muted-foreground leading-relaxed">{description}</p>
                    </div>
                </CardContent>
            </Card>
        </Link>
    )
}
