
"use client";

import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { AlertCircle, Clock, CheckCircle, TrendingUp, FileText, Filter, ChevronRight, Shield } from "lucide-react";
import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';

export const AuthorityDashboard = () => {
    const { user } = useAuth();
    const [stats, setStats] = useState({
        total: 0,
        pending: 0,
        inProgress: 0,
        resolved: 0,
        highPriority: 0,
        byCategory: {} as Record<string, number>
    });
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchStats = async () => {
            if (!user) return;
            try {
                const token = await user.getIdToken();
                const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

                const response = await fetch(`${baseUrl}/grievances/stats`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });

                if (!response.ok) throw new Error('Failed to fetch stats');
                const data = await response.json();
                setStats(data);
            } catch (error) {
                console.error('Error fetching stats:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchStats();
    }, [user]);

    return (
        <div className="space-y-10 animate-in fade-in-50 duration-500 pb-10">
            <header>
                <h2 className="text-3xl font-bold tracking-tight mb-2 text-slate-900 dark:text-slate-50">Authority Command Center</h2>
                <p className="text-muted-foreground">Monitor campus grievances, prioritize critical issues, and manage resolution workflows.</p>
            </header>

            {/* Quick Stats */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <StatCard
                    title="Total Grievances"
                    value={loading ? "..." : stats.total}
                    icon={FileText}
                    color="text-blue-600"
                    bgColor="bg-blue-50 dark:bg-blue-900/20"
                />
                <StatCard
                    title="Pending Review"
                    value={loading ? "..." : stats.pending}
                    icon={Clock}
                    color="text-amber-600"
                    bgColor="bg-amber-50 dark:bg-amber-900/20"
                />
                <StatCard
                    title="In Progress"
                    value={loading ? "..." : stats.inProgress || 0}
                    icon={TrendingUp}
                    color="text-indigo-600"
                    bgColor="bg-indigo-50 dark:bg-indigo-900/20"
                />
                <StatCard
                    title="Resolved cases"
                    value={loading ? "..." : stats.resolved}
                    icon={CheckCircle}
                    color="text-emerald-600"
                    bgColor="bg-emerald-50 dark:bg-emerald-900/20"
                />
            </div>

            {/* Quick Actions */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <Card className="bg-gradient-to-br from-primary/[0.03] to-transparent border-primary/10 shadow-sm">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2 text-lg font-bold">
                            <AlertCircle className="w-5 h-5 text-primary" />
                            Resolution Center
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <Link href="/authority/grievances?status=Submitted" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <Clock className="w-4 h-4 mr-3 text-primary" />
                                    Review Pending Cases ({stats.pending})
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                        <Link href="/authority/grievances?priority=High" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <AlertCircle className="w-4 h-4 mr-3 text-primary" />
                                    Prioritize High-Impact Issues
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                        <Link href="/authority/grievances" className="block group">
                            <Button className="w-full justify-between transition-all group-hover:shadow-md" variant="outline">
                                <span className="flex items-center">
                                    <FileText className="w-4 h-4 mr-3 text-primary" />
                                    Access All History
                                </span>
                                <ChevronRight className="w-4 h-4 text-muted-foreground/50 group-hover:translate-x-1 transition-transform" />
                            </Button>
                        </Link>
                    </CardContent>
                </Card>

                {/* Category Distribution */}
                <Card className="shadow-sm border-border/50">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2 text-lg font-bold">
                            <Filter className="w-5 h-5 text-primary" />
                            Impact Areas
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-2">
                            {Object.entries(stats.byCategory).length > 0 ? (
                                Object.entries(stats.byCategory).map(([category, count]) => (
                                    <div key={category} className="flex items-center justify-between p-3 rounded-xl hover:bg-muted/50 transition-colors border border-transparent hover:border-border/50">
                                        <span className="text-sm font-semibold">{category}</span>
                                        <Badge variant="secondary" className="font-bold">{count}</Badge>
                                    </div>
                                ))
                            ) : (
                                <div className="text-center py-10">
                                    <AlertCircle className="w-8 h-8 text-muted-foreground/20 mx-auto mb-2" />
                                    <p className="text-sm text-muted-foreground">No active grievances in categories</p>
                                </div>
                            )}
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Status Overview */}
            <Card className="shadow-sm border-border/50">
                <CardHeader>
                    <CardTitle className="text-lg font-bold">Workflow Health</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        <StatusOverviewCard
                            status="Pending"
                            count={stats.pending}
                            total={stats.total}
                            color="bg-amber-500"
                        />
                        <StatusOverviewCard
                            status="In Progress"
                            count={stats.inProgress || 0}
                            total={stats.total}
                            color="bg-indigo-500"
                        />
                        <StatusOverviewCard
                            status="Resolved"
                            count={stats.resolved}
                            total={stats.total}
                            color="bg-emerald-500"
                        />
                    </div>
                </CardContent>
            </Card>
        </div>
    );
};

const StatCard = ({ title, value, icon: Icon, color, bgColor }: any) => (
    <Card className="border-border/50 shadow-sm hover:shadow-md transition-all duration-200">
        <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
                <div className={`p-2.5 rounded-xl ${bgColor} ${color}`}>
                    <Icon className="w-5 h-5" />
                </div>
            </div>
            <div>
                <p className="text-sm font-medium text-muted-foreground mb-1">{title}</p>
                <div className="text-3xl font-bold tracking-tight">{value}</div>
            </div>
        </CardContent>
    </Card>
);

const StatusOverviewCard = ({ status, count, total, color }: any) => {
    const percentage = total > 0 ? Math.round((count / total) * 100) : 0;

    return (
        <div className="p-5 border border-border/50 rounded-2xl bg-slate-50/50 dark:bg-slate-900/50">
            <div className="flex items-center justify-between mb-3">
                <span className="text-sm font-bold text-muted-foreground uppercase tracking-wider">{status}</span>
                <span className="text-2xl font-black">{count}</span>
            </div>
            <div className="w-full bg-muted rounded-full h-2.5 overflow-hidden mb-2">
                <div
                    className={`h-full ${color} transition-all duration-700 rounded-full`}
                    style={{ width: `${percentage}%` }}
                />
            </div>
            <p className="text-[10px] font-bold text-muted-foreground/70">{percentage}% of all cases</p>
        </div>
    );
};
